import { getSeckillConfig, getSeckillList as _getSeckillList } from "@api/activity"; // import CountDown from "@components/CountDown";

import Loading from "@components/Loading";
export default {
  name: "GoodsSeckill",
  components: {
    // CountDown,
    Tab: _Tab,
    Tabs: _Tabs,
    Loading: Loading
  },
  props: {},
  data: function data() {
    return {
      timeList: [],
      sticky: false,
      loading: false,
      datatime: 0,
      active: 0,
      seckillList: [],
      status: false,
      //砍价列表是否获取完成 false 未完成 true 完成
      loadingList: false,
      //当前接口是否请求完成 false 完成 true 未完成
      page: 1,
      //页码
      limit: 5 //数量

    };
  },
  mounted: function mounted() {
    this.mountedStart();
  },
  methods: {
    mountedStart: function mountedStart() {
      var _this = this;

      var that = this;
      getSeckillConfig().then(function (res) {
        that.$set(that, "timeList", res.data.seckillTime);
        that.$set(that, "active", res.data.seckillTimeIndex);
        that.datatime = that.timeList[that.active].stop;
        that.loadingList = false;
        that.getSeckillList();
        that.$nextTick(function () {
          that.sticky = true;
          that.$refs.timeList.scrollIntoView();
        });
      });
      this.$scroll(this.$refs.container, function () {
        !_this.loadingList && _this.getSeckillList();
      });
      setTimeout(function () {
        that.loading = true;
      }, 500);
    },
    setTime: function setTime(index) {
      var that = this;
      that.active = index;
      that.datatime = that.timeList[that.active].stop;
      that.seckillList = [];
      that.page = 1;
      that.status = false;
      that.loadingList = false;
      that.getSeckillList();
    },
    getSeckillList: function getSeckillList() {
      var that = this;
      if (that.loadingList) return;
      if (that.status) return;
      that.loadingList = true;
      var time = that.timeList[that.active].id;

      _getSeckillList(time, {
        page: that.page,
        limit: that.limit
      }).then(function (res) {
        that.status = res.data.length < that.limit;
        that.seckillList.push.apply(that.seckillList, res.data);
        that.page++;
        that.loadingList = false;
      }).catch(function () {
        that.loadingList = false;
      });
    },
    goDetail: function goDetail(id, status) {
      var that = this;
      var time = that.timeList[that.active].stop;
      this.$router.push({
        path: "/activity/seckill_detail/" + id + "/" + time + "/" + status
      });
    }
  }
};